/* ------------------------------------ *\
    #ICON OVERLAY
\* ------------------------------------ */


.o-icon-overlay {
    position: relative;
}

.o-icon-overlay__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
