///
/// Link Pseudo States
///
/// @param {boolean} $parent [true] - Can include the parent selector too if set to
///
@mixin link-states($parent: false) {
    @if ($parent) {
        &,
        &:hover,
        &:active,
        &:focus {
            @content;
        }
    }

    @else {
        &:hover,
        &:active,
        &:focus {
            @content;
        }
    }
}
