/* ------------------------------------ *\
    #CONTAINER
\* ------------------------------------ */

// Container Width
$container-width:       1440px;

// Container left and right padding
$container-padding:       18px;


//
// An outer container for site content. A container should never be a child or
// instance of itself.
//
.o-container {
    padding: 0 $container-padding;
    margin: 0 auto;
    width: 100%;
    max-width: $container-padding + $container-width + $container-padding;

    @include media('sm') {
        width: 90%;
    }
}
